@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
select.searchBy {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 17px;
    border-radius: 20px;
    border: none;
    padding: 4px 0 4px 8px;
    color: #CC5500;
}


@media all and (min-width: 1000px) {
    select.searchBy {
        font-size: 22px;
    }
}
.searchBar {
    font-family: 'Roboto Condensed', sans-serif;
    background-color: rgba(169,169,169,.85);
    text-align: center;
    width: 70%;
    margin: 20px auto 20px auto;
    padding: 15px 0;
    border-radius: 20px;
    color: #CC5500;
    font-size: 18px;
}

#searchInput {
    margin: 2px 0 10px 0;
    height: 25px;
    border-radius: 20px;
    border: none;
}

.submit {
    margin-top: 10px;
    padding: 3px 10px;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 18px;
    border-radius: 20px;
    border: none;
    background-color: #CC5500;
}

@media all and (min-width: 1000px) {
    .searchBar {
        width: 30%;
        font-size: 25px;
    }

    #searchInput {
        height: 30px;
        width: 250px;
        font-size: 22px;
    }

    .submit {
        font-size: 25px;
        padding: 6px 15px;
    }
}
.results {
    display: flex;
    overflow: hidden;
    justify-content: center;
    margin: 0;
    height: 515px;
    overflow-y: scroll;
    direction:rtl; 
}

.resultsList {
    width: 70%;
    padding: 5px;
    font-family: 'Roboto Condensed', sans-serif;
}

.listItem {
    background-color: rgba(169,169,169,.85);
    border-radius: 20px;
    padding: 10px 0;
    margin-bottom: 40px;
}

.result-name {
    font-size: 20px;
}

.listItem p{
    text-align: center;
    margin: 0 0 5px 0;
    font-size: 17px;
    color: #CC5500;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: grey; 
    border-radius: 10px;
}

@media all and (min-width: 1000px) {
    .results {
        height: 620px;
    }
    .resultsList {
        width: 50%;
        margin: 0;
    }

    .listItem p {
        font-size: 25px;
    }



}
h1 {
    font-family: 'Roboto Condensed', sans-serif;
    background-color: rgba(169,169,169,.85);
    border-radius: 20px;
    max-width: 600px;
    margin: 10px auto;
    text-align: center;
    font-size: 30px;
    padding: 30px 0 30px 0;
    width: 90%;
    color: #CC5500;
}

@media all and (min-width: 1000px) {

    h1 {
        font-size: 60px;
    }
}
.error {
    background-color: rgba(169,169,169,.85);
    font-family: 'Roboto Condensed', sans-serif;
    width: 30%;
    margin: 50px auto;
    border-radius: 20px;
    padding: 30px;
    margin-bottom: 40px;
    font-size: 30px;
    color: #CC5500;
    text-align: center;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html {
  height: 100%;
  background-image: url(/static/media/luke-leia-background.45fa45a2.jpg);
  background-position: center;
  position: relative;
  background-repeat: no-repeat;
}
